import Topbar from '../../components/topBar/topbarx.vue'
export default {
	data() {
		return {
			title:'验证原手机号',
			topbarData: {
				leftHandle: false,
				title: "验证原手机号",
				left: "《",
				right: ""
			},
		}
	},
	methods: {
		childbar(val) {
			switch (val.identify) {
				case "leftHandle":
					break;
				case "rightHandle":
					this.$router.push({
						path: "/pwdreg",
						query: {
							tag: "reg",
							btn: "reg"
						}
					});
					break;
			}
		},
		// 返回上级目录
		goBack(){
			uni.switchTab({
				url: '../mine/mine'
			})
		}
	},
	components: {
		Topbar
	}
}
